"use client";

import {
  Connector,
  useAccount,
  useConnect,
  useDisconnect,
} from "@starknet-react/core";
import { StarknetkitConnector, useStarknetkitConnectModal } from "starknetkit";

const buttonStyles =
  "bg-[#e2a127] border-black border rounded-lg p-2 transition-shadow hover:shadow-[6px_6px_0_0_rgba(0,0,0,1)] cursor-pointer";

import React, { useEffect, useState } from "react";

export function WalletConnectorModal() {
  const [showOptions, setShowOptions] = useState(false);
  const { disconnect } = useDisconnect();

  const { connect, connectors } = useConnect();
  const { starknetkitConnectModal } = useStarknetkitConnectModal({
    connectors: connectors as StarknetkitConnector[],
  });

  async function connectWallet() {
    const { connector } = await starknetkitConnectModal();
    if (!connector) {
      return;
    }
    await connect({ connector: connector as Connector });
  }

  const { address } = useAccount();

  const optionsRef = React.useRef<HTMLDivElement>(null);

  // Sync state when account changes
  useEffect(() => {
    setShowOptions(false);
    // You can add more sync logic here if needed
  }, [address]);

  useEffect(() => {
    if (!showOptions) return;

    function handleClickOutside(event: MouseEvent) {
      if (
        optionsRef.current &&
        !optionsRef.current.contains(event.target as Node)
      ) {
        setShowOptions(false);
      }
    }

    document.addEventListener("mousedown", handleClickOutside);
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, [showOptions]);

  if (!address) {
    return (
      <button onClick={connectWallet} className={buttonStyles}>
        Connect Wallet
      </button>
    );
  }
  return (
    <div className="relative flex flex-col gap-2">
      <button
        onClick={() => setShowOptions((prev) => !prev)}
        className={buttonStyles}
      >
        {address?.slice(0, 8)}...{address?.slice(-4)}
      </button>
      {showOptions && (
        <div
          ref={optionsRef}
          className="absolute top-full right-0 mt-2 min-w-[200px] bg-white border border-black rounded-lg shadow-lg z-10 p-1"
        >
          <button
            onClick={() => {
              navigator.clipboard.writeText(address!);
              setShowOptions(false);
            }}
            className="block w-full text-left px-4 py-2 hover:bg-gray-100 cursor-pointer "
          >
            Copy Address
          </button>
          <a
            href={`https://starkscan.co/contract/${address}`}
            target="_blank"
            rel="noopener noreferrer"
            className="block w-full text-left px-4 py-2 hover:bg-gray-100 cursor-pointer"
            onClick={() => setShowOptions(false)}
          >
            View on Explorer
          </a>
          <button
            onClick={() => {
              disconnect();
              setShowOptions(false);
            }}
            className="block w-full text-left px-4 py-2 hover:bg-gray-100 cursor-pointer"
          >
            Disconnect
          </button>
        </div>
      )}
    </div>
  );
}
